.text-white{
  color:#FFFFFF;
}

.margin {
  margin: 1.5em;
}

.margin-top{
  margin-top: 1.5em;
}

.margin-bottom{
  margin-bottom: 1.5em;
}

.padding{
  padding: 1em;
}

.border-radius{
  border-radius: 0.5em;
}

.border-radius-1{
  border-radius: 1em;
}

.border-radius-2{
  border-radius: 2em;
}


.border-gray-1{
  border: 1px solid #DDD;
}

.solid{
  display: inline-block;
}

.img-rounded{
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 0 auto;
  display: inline-block;
  margin: auto;
}


.text-ts-blue{
  color: #222662;
}

.vertical-text {
  transform: rotate(-90deg);
  height: 100px;
}

.header-principal{
  background-image: url(../img/principal.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0;
}

.logo-header{
  padding: 1em;
  border: 1px solid #fff;
  border-radius: 0.5em;
  opacity: 0.90;
  width: 80%;
  max-width: 25em;
}

.text-shadow-dark{
  text-shadow: 1px 1px 3px #333;
}

.navbar-home{
   height: 90px;
}


.navbar-nav > li > a:hover{
   color: #222662;
}

.navbar-nav >  li:first-child > a{
  border-top-left-radius: 0.7em;
  border-bottom-left-radius: 0.7em;
  border-right: 1px solid #fff;

}

.navbar-nav > li:last-child > a {
  border-top-right-radius: 0.7em;
  border-bottom-right-radius: 0.7em;
  border-left: 1px solid #fff;
}

.menu-hz{
  padding: 0.5em 1em 0;
}

.menu-hz > li{
  padding: 0 1em;

}

.menu-hz > li:hover{
  background-color: #FFF;
}



.menu-hz > li > a:hover{
  color: #222662;
}

.menu-hz >  li:first-child {
  border-top-left-radius: 0.25em;
  border-bottom-left-radius: 0.25em;
  border-right: 1px solid #fff;

}

.menu-hz > li:last-child{
  border-top-right-radius: 0.25em;
  border-bottom-right-radius: 0.25em;
  border-left: 1px solid #fff;
}
